<template>
	<div id="address-post">
		<public-header title="编辑地址"></public-header>
		<div class="content">
			<div class="list">
				<template v-if="address.store && address.store.auto_get_address == 1">
					<div class="list-item flex-lr van-hairline--bottom" v-if="!address_type">
						<div class="item-title ellipsis">小区/大厦/学校</div>
						<div class="item-input flex-lr">
							<i class="icon icon-location"></i>
							<div class="location">
								<div class="flex-lr" @click="util.jsUrl('/pages/member/addressLocation', query, 'replace')">
									<div v-if="address.address" style="color: #333">{{address.address}}</div>
									<span v-else>点击添加地址(必填)</span>
									<i class="icon icon-right"></i>
								</div>
							</div>
						</div>
						<div class="popup-search" v-if="resolveStatus">
							<template v-if="addressSearch.length > 0">
								<van-cell-group class="search-list">
									<van-cell v-for="(item,index) in addressSearch"  :title="item.address" :label="item.name" :key="index" @click="onSelectAddress(item)" v-if="index < 3"/>
									<div class="padding-15-tb text-center font-14">
										<span class="c-danger" @click="util.jsUrl('/pages/member/addressLocation?key='+ addressResult.address, query, 'replace')">查找更多</span>
									</div>
								</van-cell-group>
							</template>
							<template v-else>
								<van-cell-group class="search-list">
									<div class="padding-15-tb text-center font-14">
										<span class="c-danger" @click="util.jsUrl('/pages/member/addressLocation?key='+ addressResult.address, query, 'replace')">未找到地址，手动去搜索</span>
									</div>
								</van-cell-group>
							</template>
						</div>
					</div>
					<div class="list-item flex-lr van-hairline--bottom" v-else-if="address_type == 1">
						<div class="item-title ellipsis">选择地址</div>
						<div class="item-input flex-lr">
							<div class="w-100 font-15">
								<div class="flex-lr" @click="onToggleAddressPopupStatus">
									<div v-if="address.address" style="color: #333">{{address.address}}</div>
									<span class="c-gray" v-else>点击选择地址(必填)</span>
									<i class="icon icon-right c-gray"></i>
								</div>
							</div>
						</div>
					</div>
				</template>
				<template v-else>
					<div class="list-item flex-lr van-hairline--bottom">
						<div class="item-title ellipsis">收货地址</div>
						<div class="item-input">
							<input name='address' v-model="address.address" type="text" class="" placeholder="请输入收货地址">
						</div>
					</div>
				</template>

				<div class="list-item flex-lr van-hairline--bottom">
					<div class="item-title ellipsis">楼号-门牌号</div>
					<div class="item-input ">
						<input name='number' v-model="address.number" type="text" class="" placeholder="例:16号楼427室">
					</div>
				</div>
				<div class="list-item flex-lr van-hairline--bottom">
					<div class="item-title ellipsis ">联系人</div>
					<div class="item-input">
						<div class="username van-hairline--bottom">
							<input type="text" name='realname' v-model="address.realname" placeholder="您的姓名">
						</div>
						<div class="sex flex">
							<div class="sex-item flex" @click="onSelectSex('先生')">
								<i class="icon" :class="{'icon-check': sexName == '先生'}"></i>先生
							</div>
							<div class="sex-item flex" @click="onSelectSex('女士')">
								<i class="icon" :class="{'icon-check': sexName == '女士'}"></i>女士
							</div>
						</div>
					</div>
				</div>
				<div class="list-item flex-lr van-hairline--bottom">
					<div class="item-title ellipsis">手机号</div>
					<div class="item-input ">
						<input type="number" name='mobile' v-model="address.mobile" class="" placeholder="请填写手机号">
					</div>
				</div>
				<div class="list-item flex-lr van-hairline--bottom">
					<div class="item-title ellipsis">标签</div>
					<div class="tags-list">
						<div class="tags-item" :class="{active: tag == '1'}" @click="onChangeAdderssTags('1')">家</div>
						<div class="tags-item" :class="{active: tag == '2'}" @click="onChangeAdderssTags('2')">公司</div>
						<div class="tags-item" :class="{active: tag == '3'}" @click="onChangeAdderssTags('3')">学校</div>
					</div>
				</div>
				<div class="list-item flex-lr van-hairline--bottom">
					<div class="item-title ellipsis">设为默认地址</div>
					<div class="item-input flex-start-center">
						<van-switch v-model="address.is_default" size="18px" active-color="#ff2d4b" inactive-color="#EEEEEE" />
					</div>
				</div>
				<div class="address-resolve" v-if="address.address_resolve_status == 1">
					<div>
						<span class="font-bold">地址智能解析</span>
						<span class="icon icon-question1 c-gray"></span>
					</div>
					<template v-if="!focus">
						<div class="flex-lr margin-5-t" @click="focus = true">
<!--
							<div class="c-gray">粘贴文本或上传图片，自动识别地址信息</div>
-->
							<div class="c-gray">粘贴文本，自动识别地址信息</div>
							<div class="icon icon-pic font-16" v-if="0"></div>
						</div>
					</template>
					<template v-else>
						<van-field
							v-model="addressValue"
							type="textarea"
							:autosize="{maxHeight: 80, minHeight: 80}"
							class="border-0px"
							placeholder="将复制的地址信息粘贴至此"
						>
						</van-field>
						<div class="flex-lr">
							<div>
								<span class="icon icon-pic font-16" v-if="0"></span>
							</div>
							<div class="flex">
								<span class="margin-15-r" @click="onClearAddress">清空</span>
								<van-button size="small" type="danger" @click="onResolve">提交</van-button>
							</div>
						</div>
					</template>
				</div>
			</div>
			<div class="save-btn" @click="onSubmit">
				<van-button type="danger" size="normal" :block="true">保存</van-button>
			</div>
			<div class="save-btn" v-if="address && address.id > 0" @click="onDel">
				<van-button type="default" size="normal" :block="true">删除</van-button>
			</div>
		</div>
		<van-popup v-model="addressPopupStatus" position="bottom">
			<van-picker
				show-toolbar
				title="选择地址"
				value-key="title"
				:columns="columns"
				@cancel="onToggleAddressPopupStatus"
				@confirm="onConfirmAddress"
				@change="onChangePicker"
			/>
		</van-popup>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import { mapState, mapMutations } from "vuex"
	import PublicHeader from '@/components/header'
	import Uploader from '@/components/uploader'

	export default {
		data() {
			return {
				query: {},
				address: {
					address: '',
					store: {},
					show_resolve: 1,
				},
				sexName: '先生',
				submiting: 0,
				value: [],

				address_type: 0,
				area_level: 0,
				areas: [],
				columns: [
					{
						values: [],
						className: 'column1',
						defaultIndex: 0
					},
					{
						values: [],
						className: 'column2',
						defaultIndex: 0
					}
				],
				addressPopupStatus: false,
				showPreLoading: true,
				tag: '0',
				focus: false,
				addressValue: '',
				addressResult: {
					name: '',
					mobile: '',
					address: ''
				},
				resolveStatus: false,
				addressSearch: [],
			}
		},
		components: {
			PublicHeader,
			Uploader
		},
		computed: {
			...mapState(['editAddress', 'erranderExtra', 'erranderStoreExtra', 'addressUser']),

			orderCreateQuery: function() {
				var that = this;
				var params = this.query;
				if(params) {
					let query = {};
					for(let key in params) {
						let usefuls = ['sid', 'is_pindan', 'pindan_id', 'is_buysvip'];
						if(usefuls.indexOf(key) >= 0) {
							query[key] = params[key];
						}
					}
					return query;
				} else {
					return {};
				}
			}
		},
		methods: {
			...mapMutations(['replaceAddress', 'setOrderExtra', 'setState', 'setMallOrderExtra']),

			onClearAddress() {
				this.addressValue = '';
			},

			onSelectAddress(item) {
				this.replaceAddress(item);
				if(this.editAddress) {
					this.address = Object.assign(this.address, this.editAddress);
					this.resolveStatus = false;
				}
			},

			onResolve() {
				var that = this;
				if(!that.addressValue) {
					that.util.$toast('请粘贴要识别的地址');
					return false;
				}
				that.util.request({
					url: 'wmall/member/address/resolve',
					data: {
						text: that.addressValue,
					},
				}).then((res) => {
					let result = res.data.message;
					if(result.errno) {
						that.util.$toast(result.message);
						return false;
					}
					result = result.message;
					that.addressResult = that.util.extend(that.addressResult, result);
					that.address.realname = result.name;
					that.setState({type: 'addressUser', key: 'realname', val: result.name});
					that.address.mobile = result.mobile;
					that.setState({type: 'addressUser', key: 'mobile', val: result.mobile});

					if(result.address) {
						var MapType = that.util.getStorage('MapType');
						var url = 'system/common/map/suggestion';
						if(MapType == 'google') {
							url = 'system/common/map/suggestion_google';
						}
						that.util.request({
							url: url,
							data: {
								key: result.address
							}
						}).then((res) => {
							res = res.data.message;
							if(res.errno) {
								that.util.$toast(res.message);
								return false;
							}
							if(res.message && res.message.length > 0) {
								that.addressSearch = [...res.message];
							}
							that.resolveStatus = true;
						})
					}
				});
			},

			onToggleAddressPopupStatus() {
				var that = this;
				that.addressPopupStatus = !that.addressPopupStatus;
			},

			onChangePicker(picker, values, index) {
				if(index == 0) {
					if(values[0].child && values[0].child.length > 0) {
						picker.setColumnValues(1, values[0].child);
						if(this.area_level == 1) {
							if(values[0].child[0].child && values[0].child[0].child.length > 0) {
								picker.setColumnValues(2, values[0].child[0].child);
							} else {
								picker.setColumnValues(2, []);
							}
						}
					} else {
						picker.setColumnValues(1, []);
					}
				} else if(index == 1) {
					if(this.area_level == 1) {
						if(values[1].child && values[1].child.length > 0) {
							picker.setColumnValues(2, values[1].child);
						} else {
							picker.setColumnValues(2, []);
						}
					}
				}
			},

			onConfirmAddress(values, indexes) {
				var that = this;
				var index = that.area_level == 1 ? 2 : 1;
				if(values[index].status == 0) {
					var message = '亲，您的地址【' + values[index].title + '】已超出【' + that.address.store.title +'】的配送范围了，请更换其他商家下单';
					that.$dialog.confirm({
						title: '温馨提示',
						message: message,
						confirmButtonText: '更换商家',
						cancelButtonText: '调整地址',
					}).then(() => {
						that.$router.replace(that.util.getUrl({path: '/pages/home/index'}));
					}).catch(() => {});
					return;
				}
				that.address.address = values[0].title + values[1].title;
				if(index == 2) {
					that.address.address += values[2].title;
					that.address.area_parentid = values[index-2].id;
				}
				that.address.location_x = values[index].location_x;
				that.address.location_y = values[index].location_y;
				that.address.area_childid = values[index].id;
				that.address.area_id = values[index-1].id;
				that.onToggleAddressPopupStatus();
			},

			onLoad() {
				var that = this;
				this.util.request({
					url: 'wmall/member/address/post',
					data: {
						sid: this.sid,
						id: this.id,
						channel: this.channel
					},
				}).then((res) => {
					this.showPreLoading = false;
					let result = res.data.message;
					if(result.errno) {
						this.util.$toast(result.message);
					}
					this.address = result.message;
					if(this.from == 'location' && this.editAddress.address && this.editAddress.lat && this.editAddress.lng) {
						this.address = Object.assign(this.address, this.editAddress);
						//this.replaceAddress({});
					}
					if(this.addressUser && this.addressUser.realname) {
						this.address.realname = this.addressUser.realname;
					}
					if(this.addressUser && this.addressUser.mobile) {
						this.address.mobile = this.addressUser.mobile;
					}
					this.sexName = !this.address.sex ? this.sexName : this.address.sex;
					this.address_type = result.message.address_type;
					this.area_level = result.message.area_level;
					this.address.is_default = this.address.is_default == 1 ? true : false;
					if(this.address_type == 1 && result.message.areas && result.message.areas.length > 0) {
						this.areas = [...result.message.areas];
						this.columns[0].values = [...this.areas];
						if(this.area_level == 1) {
							this.columns.push({
								values: [],
								className: 'column3',
								defaultIndex: 0
							});
							var area_parent_index = result.message.area_parent_index;
							this.columns[0].defaultIndex = area_parent_index;
							if(this.areas[area_parent_index].child && this.areas[area_parent_index].child.length > 0) {
								var area_index = result.message.area_index;
								this.columns[1].values = [...this.areas[area_parent_index].child]
								this.columns[1].defaultIndex = area_index;
								if(this.areas[area_parent_index].child[area_index].child && this.areas[area_parent_index].child[area_index].child.length > 0) {
									this.columns[2].values = [...this.areas[area_parent_index].child[area_index].child];
									this.columns[2].defaultIndex = result.message.area_child_index;
								}
							}
						} else {
							var area_index = result.message.area_index;
							this.columns[0].defaultIndex = area_index
							if(this.areas[area_index].child && this.areas[area_index].child.length > 0) {
								this.columns[1].values = [...this.areas[area_index].child]
								this.columns[1].defaultIndex = result.message.area_child_index;
							}
						}
					}
					if(!this.id) {
						if(!this.editAddress.address && this.address_type != 1) {
							this.util.getLocation({
								successLocation: function(res) {
								},
								successAddress:  function(res) {
									that.address.address = res.address;
									that.address.location_x = res.location_x;
									that.address.location_y = res.location_y;
									that.address = Object.assign({}, that.address);
								},
								fail: function(res) {
								}
							});
						}
					}
					if(this.from == 'location') {
						this.replaceAddress({});
					}
					this.tag = result.message.tag;
				});
			},

			onChangeAdderssTags(tag) {
				if(this.tag == tag) {
					return;
				}
				this.tag = tag;
			},

			onSubmit() {
				if(this.submiting == 1) {
					return false;
				}
				if(!this.address.address) {
					this.util.$toast('地址不能为空');
					return false;
				}
				if(!this.address.realname) {
					this.util.$toast('联系人不能为空');
					return false;
				}
				if(!this.address.mobile) {
					this.util.$toast('手机号不能为空');
					return false;
				}
				if(!this.util.isValidMobile(this.address.mobile)) {
					this.util.$toast('手机号格式错误');
					return false;
				}
				this.submiting = 1;
				var params = {
					id: this.id,
					sid: this.sid,
					order_id: this.orderId,
					channel: this.channel,
					flag: 1,
					sex: this.sexName,
					number: this.address.number,
					realname: this.address.realname,
					mobile: this.address.mobile,
					address:  this.address.address,
					location_x:  this.address.location_x || this.address.lat,
					location_y:  this.address.location_y || this.address.lng,
					area_childid: this.address.area_childid,
					area_id: this.address.area_id,
					area_parentid: this.address.area_parentid,
					tag: this.tag,
					is_default: this.address.is_default ? 1 : 0,
				};
				if(this.erranderExtra && this.erranderExtra.agentid) {
					params['agentid'] = this.erranderExtra.agentid;
				}
				if(this.erranderStoreExtra && this.erranderStoreExtra.agentid) {
					params['agentid'] = this.erranderStoreExtra.agentid;
				}
				this.util.request({
					url: 'wmall/member/address/post',
					data: params,
					method: 'POST',
				}).then((res) => {
					this.submiting = 0;
					let result = res.data.message;
					if(!this.channel || this.channel == '' || this.channel == 'undefined') {
						if(result.errno) {
							this.util.$toast(result.message);
							return false;
						}
						this.replaceAddress({});
						this.util.$toast('保存地址成功', this.util.getUrl({path: '/pages/member/address'}), 1500, 'replace');
						return;
					} else if(this.channel == 'takeout') {
						if(result.errno == -1000) {
							if(this.address_type == 1) {
								this.$dialog.confirm({
									title: '温馨提示',
									message: '亲，您的地址已超出商家的配送范围了，请更换商家下单',
									confirmButtonText: '更换商家',
									cancelButtonText: '调整地址',
								}).then(() => {
									this.$router.replace(this.util.getUrl({path: '/pages/home/index'}));
								}).catch(() => {});
							} else {
								this.$dialog.confirm({
									title: '温馨提示',
									message: result.message,
									confirmButtonText: '调整地址',
									cancelButtonText: '仍然保存',
								}).then(() => {
								}).catch(() => {
									params.force = 1;
									this.util.request({
										method: 'POST',
										url: "wmall/member/address/post",
										data: params,
									}).then((res) => {
										if(this.orderId && this.orderId > 0) {
											this.$router.replace(this.util.getUrl({path: '/pages/order/address', query: {id: this.orderId}}));
										} else {
											this.replaceAddress({});
											this.setOrderExtra({key: 'address_id', val: 0});
											this.$router.replace(this.util.getUrl({path: '/pages/order/create', query: this.orderCreateQuery}));
										}
									});
								});
							}
						} else {
							if(result.errno) {
								this.util.$toast(result.message);
								return false;
							}
							if(this.orderId && this.orderId > 0) {
								this.$router.replace(this.util.getUrl({path: '/pages/order/address', query: {id: this.orderId}}));
							} else {
								this.setOrderExtra({key: 'address_id', val: result.message});
								this.$router.replace(this.util.getUrl({path: '/pages/order/create', query: this.orderCreateQuery}));
							}
						}
					} else if(this.channel == 'errander') {
						if(result.errno == -1000) {
							this.$dialog.confirm({
								title: '温馨提示',
								message: '亲,您的地址已超出跑腿的服务范围了!',
								confirmButtonText: '调整地址',
								cancelButtonText: '仍然保存',
							}).then(() => {
							}).catch(() => {
								params.force = 1;
								this.util.request({
									method: 'POST',
									url: "wmall/member/address/post",
									data: params,
								}).then((res) => {
									this.replaceAddress({});
									if(this.mode == 'take') {
										this.setState({type: 'erranderStoreExtra', key: 'buyaddress_id', val: 0});
										this.$router.replace(this.util.getUrl({path: '/plugin/pages/paotui/store/order/diy', query:{id: this.erranderId, sid: this.esid, mode: this.mode}}));
									} else if(this.mode == 'send') {
										this.setState({type: 'erranderStoreExtra', key: 'acceptaddress_id', val: 0});
										this.$router.replace(this.util.getUrl({path: '/plugin/pages/paotui/store/order/diy', query:{id: this.erranderId, sid: this.esid, mode: this.mode}}));
									} else {
										if(this.input == 'accept') {
											this.setState({type: 'erranderExtra', key: 'acceptaddress_id', val: 0});
										} else if(this.input == 'buy') {
											this.setState({type: 'erranderExtra', key: 'buyaddress_id', val: 0});
										}
										this.$router.replace(this.util.getUrl({path: '/plugin/pages/paotui/diy', query:{id: this.erranderId}}));
									}
								});
							});
						} else {
							if(result.errno) {
								this.util.$toast(result.message);
								return false;
							}
							if(this.mode == 'take') {
								this.setState({type: 'erranderStoreExtra', key: 'buyaddress_id', val: result.message});
								this.$router.replace(this.util.getUrl({path: '/plugin/pages/paotui/store/order/diy', query:{id: this.erranderId, sid: this.esid, mode: this.mode}}));
							} else if(this.mode == 'send') {
								this.setState({type: 'erranderStoreExtra', key: 'acceptaddress_id', val: result.message});
								this.$router.replace(this.util.getUrl({path: '/plugin/pages/paotui/store/order/diy', query:{id: this.erranderId, sid: this.esid, mode: this.mode}}));
							} else {
								if(this.input == 'accept') {
									this.setState({type: 'erranderExtra', key: 'acceptaddress_id', val: result.message});
								} else if(this.input == 'buy') {
									this.setState({type: 'erranderExtra', key: 'buyaddress_id', val: result.message});
								}
								this.$router.replace(this.util.getUrl({path: '/plugin/pages/paotui/diy', query:{id: this.erranderId}}));
							}
							return false;
						}
					} else if(this.channel == 'mall') {
						if(result.errno) {
							this.util.$toast(result.message);
							return false;
						}
						this.setMallOrderExtra({sid: 0, key: 'address_id', val: result.message});
						this.$router.replace(this.util.getUrl({path: '/mall/pages/order/create', query: {sids: this.query.sids}}));
					} else if(this.channel == 'location') {
						if(result.errno) {
							this.util.$toast(result.message);
							return false;
						}
						this.replaceAddress({});
						this.util.$toast('保存地址成功', this.util.getUrl({path: '/pages/home/location'}), 1500, 'replace');
					}
				})
			},
			onSelectSex(sexName) {
				this.sexName = sexName;
			},
			onDel() {
				this.$dialog.confirm({
					title: '温馨提示',
					message: '删除后将不可恢复,确定删除吗?'
				}).then(() => {
					this.util.request({
						url: 'wmall/member/address/del',
						data: {
							id: this.id
						},
					}).then((res) => {
						let result = res.data.message;
						if(!result.errno) {
							var query = {};
							if(this.sid > 0) {
								query = {
									sid: this.sid
								}
							}
							this.$router.replace(this.util.getUrl({path: '/pages/member/address', query: query}));
						}
					})
				});
			},
		},

		created(){
			this.query = this.util.parseQuery(this.$route.query);
			if(this.query) {
				this.id = this.query.id;
				this.sid = this.query.sid;
				this.channel = this.query.channel;
				this.input = this.query.input;
				this.erranderId = this.query.erranderId;
				this.from = this.query.from;
				this.orderId = this.query.orderId;
				if(this.sid > 0) {
					this.channel = 'takeout';
					this.query.channel = 'takeout';
				}
				this.esid = this.query.esid;
				this.mode = this.query.mode;
			}
		},

		mounted() {
			this.onLoad();
		},

	}
</script>

<style>
	#address-post .content .list{
		background-color: #fff;
		padding-left: 15px;
		margin-top: 10px;
		border-bottom: 1px solid #fff;
	}
	#address-post .content .list .list-item{
		position: relative;
		padding: 10px 15px 10px 0;
		line-height: 30px;
	}
	#address-post .content .list .list-item:nth-child(3) .item-title{
		align-self: flex-start;
	}
	#address-post .content .list .list-item .item-title{
		width: 30%;
		font-size: 14px;
	}
	#address-post .content .list .list-item .item-input{
		width: 70%;
	}
	#address-post .content .list .list-item .item-input input{
		width: 100%;
		padding-left: 5px;
		font-size: 14px;
		color: #323233;
	}
	#address-post .content .list .list-item .item-input .icon-location{
		font-size: 18px;
	}
	#address-post .content .list .list-item .item-input .location{
		width: 90%;
		font-size: 15px;
	}
	#address-post .content .list .list-item .item-input .location span{
		color: #c6c6c8;
	}
	#address-post .content .list .list-item .item-input .location .icon{
		color: #e0e0e0;
		font-size: 16px;
	}
	#address-post .content .list .list-item .item-input .username{
		padding-bottom: 10px;
	}
	#address-post .content .list .list-item .item-input .sex{
		padding-top: 10px;
	}
	#address-post .content .list .list-item .sex-item{
		margin-right: 20px;
		font-size: 14px;
	}
	#address-post .content .list .list-item .sex-item .icon{
		width: 18px;
		height: 18px;
		border-radius: 50%;
		border: 1px solid #ccc;
		text-align: center;
		line-height: 16px;
		margin-right: 5px;
	}
	#address-post .content .list .list-item .sex-item .icon.icon-check{
		color: #fff;
		background-color: #ff2d4b;
		border-color: #ff2d4b;
	}
	#address-post .content .list .list-item .tags-list{
		width: 75%;
		display: flex;
		align-items: center;
		flex: 1;
		padding-left: 5px;
		line-height: 1;
	}
	#address-post .content .list .list-item .tags-list .tags-item{
		width: 40px;
		text-align: center;
		font-size: 14px;
		padding: 4px 0;
		border: 1px solid #f5f5f5;
		border-radius: 3px;
		margin-right: 15px;

	}
	#address-post .content .list .list-item .tags-list .tags-item.active{
		background-color: #FF2D4B;
		color: #fff;
		border: 1px solid #f5f5f5;
	}
	#address-post .content .list .list-item .popup-search{
		position: absolute;
		z-index: 10;
		left: 0px;
		right: 15px;
		top: 100%;
		box-shadow: 0px 0px 7px #333;
		background-color: #fff;
	}
	#address-post .content .list .list-item .popup-search:before{
		position: absolute;
		display: inline-block;
		top: -5px;
		left: 49%;
		width: 0;
		height: 0px;
		content: '';
		border-style: solid;
		border-width: 10px;
		border-color: #fff #fff transparent transparent;
		transform: rotate(-45deg);
		box-shadow: 0px 0px 7px #333;
	}
	#address-post .content .save-btn{
		margin: 15px 10px;
	}
	#address-post .content .address-resolve{
		margin: 15px 15px 15px 0;
		padding: 15px;
		font-size: 14px;
		border-radius: 5px;
		background-color: #F5F5F5;
	}
	#address-post .content .address-resolve .van-cell{
		padding: 0px!important;
		background-color: #F5F5F5!important;
	}
</style>



